import React from 'react';
import { Link, Routes, Route } from 'react-router-dom';
import Home from './Home';
import BookList from './BookList';
import BookDetailView from './BookDetailView';
import BookInsert from './BookInsert';
import BookUpdate from './BookUpdate';
import BookSearch from './BookSearch';
import ChartDB from './ChartDB'
import Chart from './Chart'
import Upload from './Upload';
import UploadResult from './UploadResult';
import SignIn from './SignIn';
import Join from './Join';

const Top = () => {
    const onLogout = () => {
        localStorage.clear();
        window.location.replace('http://localhost:3000/')
    };

    return (
        <div id="top">
            <ul>
                <li>
                    <Link to="/">[]</Link>
                    <Link to="/bookList">[ ]</Link>
                    <Link to="/bookInsert">[ ]</Link>
                    <Link to="/bookSearchForm">[ ]</Link>
                    <Link to="/chart">[]</Link>
                    <Link to="/chart_db">[ (DB)]</Link>
                    <Link to="/upload">[ ]</Link>
                    <Link to="/register">[]</Link>
                    {
                        //   []
                        //   [] 
                        localStorage.getItem("token") === null ?
                            <Link to="/login">[]</Link> :
                            <Link onClick={onLogout}>[]</Link>
                    }
                </li>
            </ul>
            <hr />
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/bookList" element={<BookList />} />
                <Route path="/bookDetailView/:bookno" element={<BookDetailView />} />
                <Route path="/bookInsert" element={<BookInsert />} />
                <Route path="/bookUpdate/:bookno" element={<BookUpdate />} />
                <Route path="/BookSearchForm" element={<BookSearch />} />
                <Route path="/chart_db" element={<ChartDB />} />
                <Route path="/chart" element={<Chart />} />
                <Route path="/upload" element={<Upload />} />
                <Route path="/uploadResult" element={<UploadResult />} />
                <Route path="/login" element={<SignIn />} />
                <Route path="/register" element={<Join />} />
            </Routes>
        </div>
    );
};

export default Top;