import React from 'react';
import {
    Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis,
} from 'recharts';

const data = [
    {
        subject: '', A: 120, B: 110, fullMark: 150,
    },
    {
        subject: '', A: 98, B: 130, fullMark: 150,
    },
    {
        subject: '', A: 86, B: 130, fullMark: 150,
    },
    {
        subject: '', A: 99, B: 100, fullMark: 150,
    },
    {
        subject: '', A: 85, B: 90, fullMark: 150,
    },
    {
        subject: ' ', A: 65, B: 85, fullMark: 150,
    },
];

const Chart = () => {
    return (
        <div id="chart">
            <RadarChart cx={300} cy={250} outerRadius={150} width={500} height={500} data={data}>
                <PolarGrid />
                <PolarAngleAxis dataKey="subject" />
                <PolarRadiusAxis />
                <Radar name="Mike" dataKey="A" fill="magenta" fillOpacity={0.6} />
            </RadarChart>
        </div>
    );
};

export default Chart;