import axios from 'axios';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';

const BookListItem = ({ book }) => {
    let history = useNavigate();

    //    
    const onDeleteItem = () => {
        if (window.confirm("?")) {
            axios.delete('http://localhost:8000/book_app/book/' + book.bookno)
                .then(
                    () => {
                        history('/bookList');
                        window.location.reload();
                        // reload  
                        // DB     
                    }
                ).catch(err => console.log(err));
        }

    };

    return (
        <tr>
            <td><Link to={"/bookDetailView/" + book.bookno}>{book.bookno}</Link></td>
            <td>{book.bookname}</td>
            <td>{book.bookauthor}</td>
            <td>{book.bookprice}</td>
            <td>{book.bookdate}</td>
            <td>{book.bookstock}</td>
            <td>{book.pubno}</td>
            <td><Link to={"/bookUpdate/" + book.bookno}><button></button></Link></td>
            <td><button onClick={onDeleteItem}></button></td>
        </tr>
    );
};

export default BookListItem;