import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const BookInsert = () => {
    let history = useNavigate();

    // state   
    const [book, setBook] = useState({
        bookno: '',
        bookname: '',
        bookauthor: '',
        bookprice: '',
        bookstock: '',
        bookdate: '',
        pubno: ''
    });

    const onChange = (e) => {
        const { value, name } = e.target; // e.target name value 
        setBook({
            ...book, //  prd   
            [name]: value // name    value 
        });

    };

    //    
    const onReset = () => {
        setBook({
            bookno: '',
            bookname: '',
            bookauthor: '',
            bookprice: '',
            bookstock: '',
            bookdate: '',
            pubno: ''
        })
    };


    // submit   
    const onSubmit = (e) => {
        e.preventDefault();

        var frmData = new FormData(document.frmInsert);
        axios.post('http://localhost:8000/book_app/books/', frmData)
            .then(
                response => {
                    alert(" ");
                    history('/bookList'); //     
                }
            )
    };


    return (
        <div>
            <h3> </h3>
            <form name="frmInsert" onSubmit={onSubmit} onReset={onReset}>
                <table id="insert">
                    <thead>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookno"
                                value={book.bookno}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookname"
                                value={book.bookname}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookauthor"
                                value={book.bookauthor}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookprice"
                                value={book.bookprice}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookdate"
                                value={book.bookdate}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookstock"
                                value={book.bookstock}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="pubno"
                                value={book.pubno}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2">
                                <input type="submit" value="" />
                                <input type="reset" value="" />
                            </td>
                        </tr>
                    </thead>
                </table>
            </form>
        </div>
    );
};

export default BookInsert;