import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';

const BookDetailView = () => {
    //   
    const { bookno } = useParams();

    // state 
    const [book, setBook] = useState({
        // bookno: '', //     
        bookno: '',
        bookname: '',
        bookauthor: '',
        bookprice: '',
        bookstock: '',
        bookdate: '',
        pubno: ''
    });

    //    
    // state  
    const loadData = async () => {
        // const response = await axios.get('http://localhost:8000/book_app/book/' + bookno);
        const response = await axios.get(`http://localhost:8000/book_app/book/${bookno}`);
        console.log("detail : ", response.data);
        setBook({
            // bookno: response.data.bookno,
            bookname: response.data.bookname,
            bookauthor: response.data.bookauthor,
            bookprice: response.data.bookprice,
            bookdate: response.data.bookdate,
            bookstock: response.data.bookstock,
            pubno: response.data.pubno
        });
    }


    //    
    //  : loadData()   
    useEffect(() => {
        loadData();
    }, []);

    return (
        <div>
            <h3>   </h3>
            <table id="detail" border="1" width="500">
                <thead>
                    <tr><th></th><td> {bookno}</td></tr>
                    <tr><th> </th><td> {book.bookname}</td></tr>
                    <tr><th> </th><td> {book.bookauthor}</td></tr>
                    <tr><th> </th><td>{book.bookprice}</td></tr>
                    <tr><th> </th><td> {book.bookdate}</td></tr>
                    <tr><th> </th><td> {book.bookstock}</td></tr>
                    <tr><th> </th><td> {book.pubno}</td></tr>
                </thead>
            </table>
        </div>
    );
};

export default BookDetailView;