import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Join = () => {
    let history = useNavigate();

    // state   
    const [user, setUser] = useState({
        username: '',
        password: '',
        password2: '',
        email: '',
        user_name: '',
        user_phone: '',
        user_address: ''
    });

    const onChange = (e) => {
        const { value, name } = e.target; // e.target name value 
        setUser({
            ...user, //  prd   
            [name]: value // name    value 
        });

    };

    //    
    const onReset = () => {
        setUser({
            username: '',
            password: '',
            password2: '',
            email: '',
            user_name: '',
            user_phone: '',
            user_address: ''
        })
    };


    // submit   
    const onSubmit = (e) => {
        e.preventDefault();

        var frmData = new FormData(document.frmJoin);
        axios.post('http://localhost:8000/users/register/', frmData)
            .then(
                response => {
                    alert(" ");
                    history('/login'); //   
                },
                error => {
                    alert(error.response.data['password']);
                    // {"password": "   "}) 
                }
            )
    };

    return (
        <div>
            <h3></h3>
            <form name="frmJoin" onSubmit={onSubmit} onReset={onReset}>
                <table id="join_insert">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <td> <input
                                type="text"
                                name="username"
                                value={user.username}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="password"
                                name="password"
                                value={user.password}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td> <input
                                type="password"
                                name="password2"
                                value={user.password2}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="user_name"
                                value={user.user_name}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="email"
                                value={user.email}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="user_phone"
                                value={user.user_phone}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="user_address"
                                value={user.user_address}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2">
                                <input type="submit" value="" />
                                <input type="reset" value="" />
                            </td>
                        </tr>
                    </thead>
                </table>
            </form>
        </div>
    );
};

export default Join;