import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';

const BookUpdate = () => {
    // (1) Part1 : bookno    
    //  detailView 

    //   
    const { bookno } = useParams();

    // state 
    const [book, setBook] = useState({
        // bookno: '', //     
        bookname: '',
        bookauthor: '',
        bookprice: '',
        bookdate: '',
        bookstock: '',
        pubno: ''
    });

    //    
    // state  
    const loadData = async () => {
        // const response = await axios.get('http://localhost:8000/bokk_app/book/' + bookno);
        const response = await axios.get(`http://localhost:8000/book_app/book/${bookno}`);
        console.log("detail : ", response.data);
        setBook({
            // bookno: response.data.bookno,
            bookname: response.data.bookname,
            bookauthor: response.data.bookauthor,
            bookprice: response.data.bookprice,
            bookdate: response.data.bookdate,
            bookstock: response.data.bookstock,
            pubno: response.data.pubno
        });
    }

    //    
    //  : loadData()   
    useEffect(() => {
        loadData();
    }, []);


    // (2) Part2 :       DB 
    // insert  

    let history = useNavigate();

    const onChange = (e) => {
        const { value, name } = e.target; // e.target name value 
        setBook({
            ...book, //  prd   
            [name]: value // name    value 
        });

    };

    //    
    const onReset = () => {
        setBook({
            bookno: book.bookno,
            bookname: '',
            bookauthor: '',
            bookprice: '',
            bookdate: book.bookdate,
            bookstock: '',
            pubno: book.pubno
        })
    };

    // submit   
    const onSubmit = (e) => {
        e.preventDefault();

        var frmData = new FormData(document.frmUpdate);
        axios.put(`http://localhost:8000/book_app/book/${bookno}/`, frmData)
            .then(
                response => {
                    alert(" ");
                    history('/bookList');//     
                }
            )
    };

    return (
        <div>
            <h3>  </h3>
            <form name="frmUpdate" onSubmit={onSubmit} onReset={onReset}>
                <table id="insert">
                    <thead>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookno"
                                value={bookno}
                                readOnly />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookname"
                                value={book.bookname}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookauthor"
                                value={book.bookauthor}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookprice"
                                value={book.bookprice}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookdate"
                                value={book.bookdate}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="bookstock"
                                value={book.bookstock}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td> <input
                                type="text"
                                name="pubno"
                                value={book.pubno}
                                onChange={onChange} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2">
                                <input type="submit" value="" />
                                <input type="reset" value="" />
                            </td>
                        </tr>
                    </thead>
                </table>
            </form>
        </div>
    );
};

export default BookUpdate;